* {
  box-sizing: border-box;
}

.Leaderboard {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 73px;

  .Leaderboard-box {
    width: 1250px;

    .tab-day {
      display: flex;
      width: 254px;
      justify-content: space-between;

      .day-btn {
        width: 122px;
        height: 40px;
        color: #E0ECFF;
        background: rgba(85, 119, 253, 0.1);
        border-radius: 5.125px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
      }

      .active {
        background: #5577FD;
      }
    }

    .my-historical-trading {
      width: 1250px;
      height: 328px;
      border: 1px solid#203B60;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .title-tab-day {
        width: 1143px;
        height: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .title-leaderboard {
          font-weight: 400;
          font-size: 32px;
          color: #FFFFFF;
        }

      }

      .my-info-list {
        width: 1143px;
        display: flex;
        border: 1px solid #203B60;
        border-right: 0;
        margin-top: 50px;

        .info-box {
          height: 136px;
          display: flex;
          flex: 1;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          border-right: 1px solid #203B60;
          background: rgba(85, 119, 253, 0.1);

          .info-num {
            font-weight: 600;
            font-size: 28px;
            color: #E0ECFF;
          }
        }
      }
    }

    .rankings {
      margin-top: 40px;
      width: 1250px;
      padding: 0 55px;
      padding-bottom: 50px;
      border: 1px solid #203B60;
      margin-bottom: 40px;
      background: linear-gradient(105.59deg, rgba(97, 122, 255, 0.1) 1.23%, rgba(223, 229, 255, 0) 51.13%, rgba(85, 91, 237, 0.05) 100%);

      .rankings-title-tab-day {
        display: flex;
        justify-content: space-between;
        padding-top: 44px;

        .rankings-title {
          font-weight: 400;
          font-size: 32px;
          color: #FFFFFF;
        }
      }

      .rankings-list-title {
        display: flex;
        justify-content: space-between;
        background: #203B60;
        border-radius: 6px;
        height: 50px;
        align-items: center;
        padding: 0 34px;
        margin-top: 54px;
        font-size: 14px;

        div {
          width: 25%;
          white-space: nowrap;
        }
        
      }

      .rankings-list-num {
        background: #152446;
        padding-top: 21px;
        color: #FFFFFF;

      }

      .rankings-list-num-box {
        display: flex;
        justify-content: space-between;
        height: 35px;
        align-items: center;
        padding: 0 34px;
        font-size: 12px;

        .top-num {
          margin-left: 4px;
        }

        div {
          width: 25%;
          white-space: nowrap;
        }
        
        .up{
          color: #59AE99;
        }
        .down{
          color: #CE5E5E;
        }
      }
    }
  }

}

@media only screen and (min-width : 1140px) and (max-width:1275px) {
  .Leaderboard {
    .Leaderboard-box {
      width: 1095px;

      .my-historical-trading {
        width: 1095px;

        .title-tab-day,
        .my-info-list {
          width: 1010px;
        }

      }

      .rankings {
        width: 1095px;
      }
    }
  }

}

@media only screen and (min-width : 760px) and (max-width:1140px) {
  .Leaderboard {
    .Leaderboard-box {
      width: 771px;

      .my-historical-trading {
        width: 771px;
        height: 288px;

        .title-tab-day,
        .my-info-list {
          width: 649px;
        }

        .title-tab-day {
          flex-wrap: wrap;
          height: 84px;

          .title-leaderboard {
            margin-bottom: 30px;
          }
        }

        .my-info-list {
          height: 88px;

          .info-box {
            height: 88px;
          }
        }

      }

      .rankings {
        width: 771px;
      }
    }
  }
}

@media only screen and (max-width:760px) {
  .Leaderboard {
    .Leaderboard-box {
      width: 333px;

      .my-historical-trading {
        width: 333px;
        height: 518px;

        .title-tab-day {
          width: 282px;
          flex-wrap: wrap;
          height: 188px;

        }

        .my-info-list {
          width: 282px;
          display: block;
          border: 0;
          margin-top: 0px;

          .info-box {
            width: 282px;
            height: 88px;
            border: 1px solid #203B60;
            margin-top: 10px;
          }
        }
      }

      .rankings {
        width: 332px;
        padding: 32px 22px;

        .rankings-title-tab-day {
          flex-wrap: wrap;
          padding: 0;

          .tab-day {
            margin-top: 22px;
            width: 254px;
          }
        }

        .rankings-list-title {
          width: 273px;
          height: 28px;
          font-size: 8px;
          padding: 0 12px;
          .rank{
            width: 12%;
          }
        }

        .rankings-list-num {
          width: 273px;

          .rankings-list-num-box {
            font-size: 8px;
            padding: 0 12px;
            .rank{
              width: 12%;
            }
          }
        }
      }
    }
  }
}